.App {
  padding-left: 5%;
  padding-right: 5%;
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h4 {
  font-size: 18px;
}

ol {
  font-size: 18px;
}

p {
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5em;
}

b {
  font-weight: 600;
}